// PaymentPopup.jsx
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Button } from "../../components/Styles/MainStyle.js";
import { loadStripe } from "@stripe/stripe-js";
import {
  Elements,
  CardElement,
  useStripe,
  useElements,
} from "@stripe/react-stripe-js";
const stripePromise = loadStripe(
  "pk_test_51RUJzWAx6rm2q3pys9SgKUPRxNxPZ4P1X6EazNQvnPuHKOOfzGsbylaTLUktId9ANHULkwBk67jnp5aqZ9Dlm6PR00jKdDwvSq"
);

const PaymentPopupAcademy = ({
  moduledata,
  paytmmodule,
  show,
  onClose,
  onSubmit,
}) => {
  var apiURLAiFile = "https://blueprintcatalyst.com/api/user/aifile/";
  document.title = "Dataroom Management & Diligence";

  const storedUsername = localStorage.getItem("UserLoginData");

  const userLogin = JSON.parse(storedUsername);

  const CheckoutForm = () => {
    useEffect(() => {
      setmainamount(paytmmodule);
    }, [paytmmodule]);
    const [mainamount, setmainamount] = useState(paytmmodule);
    const [discount, setdiscount] = useState("");
    const stripe = useStripe();
    const elements = useElements();
    const [referalCode, setreferalCode] = useState("");
    const [spinners, setspinners] = useState(false);
    const [message, setMessage] = useState("");
    const [err, seterr] = useState(false);
    const [paymentType, setpaymentType] = useState("Onetime");
    const [PayidOnetime, setPayidOnetime] = useState("");

    const [buttonPay, setbuttonPay] = useState("");
    // setTimeout(() => {
    //   setMessage("");
    // }, 5000);

    const handleSubmit = async (e) => {
      e.preventDefault();

      if (!stripe || !elements) return;

      const cardElement = elements.getElement(CardElement);
      if (!cardElement) {
        setMessage("Payment form is not ready. Please reload the page.");
        seterr(true);
        return;
      }

      setspinners(true);
      try {
        // Get clientSecret from backend
        const { data } = await axios.post(
          `${apiURLAiFile}CreateuserSubscription_AcademyCheck`,
          {
            user_id: userLogin.id,
            amount: mainamount, // in EUR
          }
        );

        const result = await stripe.confirmCardPayment(data.clientSecret, {
          payment_method: {
            card: cardElement,
          },
        });

        if (result.error) {
          setMessage(result.error.message);
          seterr(true);
          setspinners(false);
        } else if (result.paymentIntent.status === "succeeded") {
          const formdata = {
            code: referalCode,
            user_id: userLogin.id,
            amount: mainamount,
            clientSecret: data.clientSecret,
            PayidOnetime: PayidOnetime,
            payment_status: result.paymentIntent.status,
            discount: discount,
          };
          await paymentsuccess(formdata);
        } else {
          setMessage("Payment failed. Try again.");
          seterr(true);
          setspinners(false);
        }
      } catch (error) {
        setMessage("Unexpected error occurred.");
        seterr(true);
        setspinners(false);
      }
    };

    const paymentsuccess = async (formdata) => {
      try {
        await axios.post(
          `${apiURLAiFile}CreateuserSubscription_Academy`,
          formdata,
          {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json",
            },
          }
        );

        setMessage("Payment successful! 🎉");
        seterr(false);
        setTimeout(() => {
          window.location.reload();
        }, 2000);
      } catch (err) {
        console.error("Success handler error:", err);
        setMessage("Payment was captured, but post-process failed.");
        seterr(true);
      } finally {
        setspinners(false);
      }
    };

    const handleRefferalCode = async (e) => {
      const upperValue = e.target.value.toUpperCase();
      setreferalCode(upperValue);
    };
    const handleapplycode = async () => {
      if (referalCode === "") {
        setbuttonPay("Enter the code");
      } else {
        let refercode = {
          code: referalCode,
          type: "Academy",
          email: userLogin.email,
        };
        console.log(refercode);
        try {
          const resp = await axios.post(
            `${apiURLAiFile}checkreferCode`,
            refercode,
            {
              headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
              },
            }
          );
          //setreferalCode(referalCode);
          if (resp.data.results.length > 0) {
            var data = resp.data.results[0];
            if (data.usage_limit > data.used_count) {
              setdiscount(data.percentage);
              const discountValue = (paytmmodule * data.percentage) / 100;
              const final = paytmmodule - discountValue;
              setmainamount(final);

              setbuttonPay("");
            } else {
              setdiscount("");
              setmainamount(paytmmodule);
              setbuttonPay("This code already used");
            }
          } else {
            setdiscount("");
            setmainamount(paytmmodule);
            setbuttonPay("Invalid code!");
          }
        } catch (err) {
        } finally {
        }
      }
    };
    return (
      <form onSubmit={handleSubmit}>
        <div
          className="form-control rounded-3"
          style={{
            padding: "0.75rem",
            border: "1px solid #000",
            borderColor: "#ced4da",
          }}
        >
          <CardElement
            options={{
              style: {
                base: {
                  fontSize: "16px",
                  color: "#32325d",
                  fontFamily:
                    '"Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
                  "::placeholder": { color: "#a0aec0" },
                  padding: "0.75rem",
                },
                invalid: {
                  color: "#e5424d",
                },
              },
              classes: {
                base: "stripe-card-element",
                focus: "border-primary",
                invalid: "border-danger",
              },
            }}
          />
        </div>

        <div className="d-flex gap-2 d-md-flex justify-content-md-end mt-4">
          <div className="d-flex flex-column">
            <input
              type="text"
              name="refferal_code"
              defaultValue={referalCode}
              onChange={handleRefferalCode}
              className="form-control w-auto"
              placeholder="Apply Referral Code"
              autoComplete="off"
              style={{ textTransform: "uppercase" }}
            />
            {buttonPay && (
              <span
                className="text-danger mt-1"
                style={{ fontSize: "0.875rem" }}
              >
                {buttonPay}
              </span>
            )}
          </div>

          <Button
            type="button"
            onClick={handleapplycode}
            className="submit d-flex align-items-center gap-2"
            style={{ background: "#5C636B", height: "fit-content" }}
          >
            Apply Code
          </Button>
        </div>
        {discount && (
          <div className="d-flex gap-2 d-md-flex justify-content-md-end mt-4">
            <b>Discount:</b> {discount}%
          </div>
        )}

        <div className="d-flex gap-2 d-md-flex justify-content-md-end mt-4">
          <Button
            disabled={!stripe || spinners}
            type="submit"
            className="submit d-flex align-items-center gap-2"
            style={{ background: "#003b21" }}
          >
            {!spinners && <span>Pay €{mainamount}</span>}
            {spinners && (
              <div
                className="spinner-border text-white spinneronetimepay m-0"
                role="status"
              >
                <span className="visually-hidden"></span>
              </div>
            )}
          </Button>
        </div>

        {message && (
          <p className={err ? " mt-3 error_pop" : "success_pop mt-3"}>
            {message}
          </p>
        )}
      </form>
    );
  };
  const [ShowPopup, setShowPopup] = useState(false);
  const handleClosepayPopup = () => {
    setShowPopup(false);
  };
  if (!show) return null;

  return (
    <div
      className="modal fade show d-block"
      style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
      tabIndex="-1"
      role="dialog"
      aria-labelledby="paymentModalLabel"
      aria-hidden="false"
    >
      <div className="modal-dialog modal-dialog-centered modal-lg">
        <div className="modal-content rounded-4 shadow-lg p-4">
          <button
            type="button"
            className="btn-close position-absolute top-0 end-0 m-3"
            onClick={onClose}
            aria-label="Close"
          ></button>

          <>
            <h2
              className="modal-title text-center fw-bold text-dark mb-4"
              id="paymentModalLabel"
            >
              Payment
            </h2>
            <div className="mb-4">
              <h5 className="fw-bold text-dark mb-2">{moduledata.name}</h5>
            </div>
            <div className="mb-4">
              <div className="fs-4 fw-semibold text-dark">
                Fee:
                <span style={{ color: "#2e5692" }} className="fw-bold">
                  €{paytmmodule}
                </span>
              </div>
              <ul className="list-group list-group-flush mt-3">
                <li className="list-group-item text-dark ps-0">
                  <strong>
                    1,200 Euros. ONE-TIME. This will include participation in
                    all modules for each company.
                  </strong>
                </li>
              </ul>
            </div>
          </>

          <div className="text-center mb-4">
            <img
              src="/assets/user/images/cardimage.jpg"
              alt="cards"
              className="img-fluid rounded"
              style={{ maxWidth: "200px" }}
            />
          </div>
          <Elements stripe={stripePromise}>
            <CheckoutForm />
          </Elements>
        </div>
      </div>
    </div>
  );
};

export default PaymentPopupAcademy;
